body {
    font-family: Arial, sans-serif;
    background-color: #f4f4f4;
    margin: 0;
    padding: 0;
}

h1 {
    text-align: center;
    color: #333;
}

.container {
    width: 80%;
    margin: auto;
    overflow: hidden;
}

.todo-list {
    list-style: none;
    padding: 0;
}

.todo-item {
    background: #fff;
    margin: 10px 0;
    padding: 10px;
    border-radius: 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.todo-item.completed {
    text-decoration: line-through;
    color: #999;
}

.header-container{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 1rem;
    gap: 10px;
}

input{
    width: 30%;
    padding: 1rem;
    font-size: 1.125rem;
    border: 1px solid #d1d5db;
    border-radius: 0.5rem;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
}
.buttons{
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    margin-left: 1rem;
}
.add-button {
    background: #007bff;
    color: white;
    border: none;
    padding: 5px 10px;
    border-radius: 5px;
    cursor: pointer;
}

button:hover {
    background: #0056b3;
}

.App {
    text-align: center;
    padding-top: 60px;
    margin: 20px;
}

.buttons {
    margin: 20px 0;
}

.cards {
    display: flex;
    justify-content: space-around;
}

.card {
    background: #fff;
    padding: 20px;
    border-radius: 20px;
    width: 45%;
    min-height: 20vh;
    max-height: 70vh;
    overflow-y: auto;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.card h2 {
    margin-top: 0;
}

ul {
    list-style: none;
    padding: 0;
}

.remove-button {
    background: #ad1919;
    color: white;
    border: none;
    /*width: 20px;*/
    /*height: 20px;*/
    padding: 5px 8px;
    border-radius: 50%;
    cursor: pointer;
    margin-left:10px;
    text-align: center;
    justify-content: center;
}

.remove-button:hover {
    background: #cc0000;
}

.todo-item{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.125rem;
    margin: 0;
}

/* Styles for mobile screens */
@media (max-width: 768px) {
    .header-container {
        flex-direction: column;
        align-items: stretch;
    }

    input{
        width: auto;
    }

    .cards {
        flex-direction: column;
        align-items: center;
    }

    .card {
        width: 90%;
        margin-bottom: 20px;
    }
}
